.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
  color: #606266;
  gap: 4rem;
  height: fit-content;
  position: relative;
}

.footer>div {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.footer_box:nth-child(1) {
  padding: 4rem 1rem;
  max-width: 1312px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: space-between
}

.footer_box:nth-child(2) {
  padding: 4rem 1rem;
  max-width: 1312px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: space-between;
  font-size: 12px;
}
.footer_box:nth-child(2) p {
  color: #191a239f;

}



@media not all and (min-width: 1024px) {
  .footer>div>div {
    flex-direction: column;
    align-items: center;
  }

  .footer>div>div>div {
    align-self: flex-start;
  }
}